<template>
    <view class="page">
        <view class="header ch-flex" @click="$nav.to('/pages/ucenter/user')">
            <view class="user-head" ></view>
            <view class="ch-flex-1">
                <view class="user-name">{{ userName }}</view>
                <view class="user-dept">工号: G1313947</view>
                <view class="user-dept">管理员</view>
            </view>
            <view>
                <u-icon name="arrow-right" color="#fff"></u-icon>
            </view>
        </view>
<!--        <view class="header">-->
<!--            <view class="header-bar">-->
<!--                <view class="status"></view>-->
<!--                <view class="lefticon" @click="$nav.to('/pages/setting/index')">-->
<!--                    <image src="/static/user.png"></image>-->
<!--                </view>-->
<!--                <view class="title">{{ $config.name }}</view>-->
<!--                <view class="righticon" @click="$nav.to('/pages/message/warning')">-->
<!--                    <image src="/static/warning.png"></image>-->
<!--                </view>-->
<!--            </view>-->

<!--            <view class="user-header">-->
<!--                <view class="head shadow-blur">-->
<!--                    <image ref="headerImg" src="../../static/1024.png"></image>-->
<!--                </view>-->
<!--                <view class="name">{{ userName }}</view>-->
<!--            </view>-->
<!--        </view>-->
        <u-gap height="20upx" bgColor="#f2f2f2"></u-gap>
        <view class="bgwhite">
            <u-cell-group :border="false">
                <u-cell :border="false" isLink :title="$t('使用帮助')" @click="$nav.to('/pages/message/webview?type=help&title=使用帮助')"></u-cell>
            </u-cell-group>
        </view>


        <u-gap height="20upx" bgColor="#f2f2f2"></u-gap>
        <view class="bgwhite">
            <u-cell-group :border="false">
                <u-cell border isLink :title="$t('关于我们')" @click="$nav.to('/pages/ucenter/about')"></u-cell>
                <u-cell border isLink :title="$t('意见反馈')" @click="$nav.to('/pages/ucenter/feedback')"></u-cell>
                <u-cell border isLink :title="$t('语言切换')" @click="$nav.to('/pages/setting/language')"></u-cell>
                <u-cell :border="false" isLink  @click="checkupdate">
                    <view slot="value">
                        <view  v-if="!btnLoading" style="font-size: 26upx; color: #666">{{appVersion}}</view>
                        <view v-else>
                            <u-loading-icon size="18"></u-loading-icon>
                        </view>
                    </view>
                    <view slot="title">
                        <view class="ch-flex"><text style="padding-right:10upx">{{ $t('版本更新') }}</text>
                            <u-badge v-if="updated" :isDot="true" bgColor="#ff3333" type="warning"></u-badge>
                        </view>
                    </view>
                </u-cell>
            </u-cell-group>
        </view>

        <u-gap height="130upx" bgColor="#f2f2f2"></u-gap>

        <view class="bgwhite logout" @click="showLogout = true">
            {{ $t('退出') }}
        </view>

        <u-modal :show="showLogout" @confirm="confirm" @cancel="cancel" :title="$config.name" showCancelButton
            :confirmText="$t('退出')" :confirmColor="$config.color.primary" :cancelText="$t('取消')" :content="$t('确定要退出当前帐号')" asyncClose></u-modal>

    </view>
</template>

<script>
const App = getApp();
export default {
    data() {
        return {
            showLogout: false,
            headerImgUrl: "",
            appVersion: "",
            updated: false,
            btnLoading: false
        }
    },
    computed: {
        userName() {
            return this.$store.state.account.userName
        }
    },
    onLoad(){
        this.appVersion = 'v.' + this.$u.sys().appVersion;
        this.updated = App.globalData.updated
    },
    onShow() {

    },
    methods: {
        checkupdate(){
            this.btnLoading = true;
            App.updates(true);
            this.$ch.sleep(500).then(() => {
                this.btnLoading = false
            })
        },
        confirm() {
            setTimeout(() => {
                this.showLogout = false
                this.$store.dispatch('logout').then(() => {
                    this.$nav.to('/pages/login/index', true)
                })
            }, 1000)
        },
        cancel() {
            this.showLogout = false
        },
        selectImage() {
            uni.chooseImage({
                count: 6,
                sizeType: ['original', 'compressed'],
                sourceType: ['album'],
                success: function (res) {
                    // 预览图片
                    uni.previewImage({
                        urls: res.tempFilePaths,
                        longPressActions: {
                            itemList: ['发送给朋友', '保存图片', '收藏'],
                            success: function (data) {
                                console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data
                                    .index + 1) + '张图片');
                            },
                            fail: function (err) {
                                console.log(err.errMsg);
                            }
                        }
                    });
                }
            });
        },
        onClickHeadImg() {

            uni.chooseImage({

                count: 1, // 头像上传1张

                sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有

                sourceType: ['album'], // 从相册选择

                success: async (chooseImageRes) => {

                    const tempFilePaths = chooseImageRes.tempFilePaths;

                    console.log('tempFilePaths', tempFilePaths)

                    console.log('chooseImageRes.tempFiles[0]', chooseImageRes.tempFiles[0])

                    this.headerImgUrl = tempFilePaths[0]
                    uni.uploadFile({

                        url: 'https://app.zlyiju.com/serverside/Upload', // 上传地址，小程序不支持接口拼接

                        filePath: tempFilePaths[0],

                        name: 'file', // 类型

                        formData: {

                            // token: data.data.body, //头像token，参考返回数据

                            // openid: this._settingConfig.openid

                            type: 1, // 后台需要的参数,type:1是图片、type:2是视频

                            path: chooseImageRes.tempFiles[0], // 后台需要的给他们路径

                        },

                        success: (uploadFileRes) => {

                            // console.log('uploadFileRes', uploadFileRes);

                            // 上传成功之后拿到后台返回的数据是个文件流,需要解析一下

                            var stra = JSON.parse(uploadFileRes.data);

                            // console.log('*******', stra.data.yj_path);

                            this.avarShow = false

                            // 请求接口

                            this.Req('PersonalData/upImg', {

                                yj_img: stra.data.yj_path

                            }, 'post')

                                .then(res => {

                                    // console.log(res.data)

                                }).catch(err => { })

                        }

                    });

                }

            })

        },
    }
}
</script>

<style lang="scss" scoped>
.header{
    background-color: $ch-theme;
    padding: 50upx 24upx;
    align-items: center;
    .user-head{
        width: 140upx;
        height: 140upx;
        border-radius: 50%;
        background-color: #f2f2f2;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        margin-right: 20upx;
    }
    .user-name{
        color: #fff;
        font-size: 44upx;
    }
    .user-dept{
        font-size: 24upx;
        color: rgba(255,255,255,.9);
    }
}
.u-badge--dot {
    width: 4px;
    height: 4px;
}

.logout {
    text-align: center;
    color: #ff3333;
    padding: 20upx 0;
    margin: 0 24upx;
}
</style>
